.greetingsContainer {
  margin-top: 1rem;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  box-sizing: content-box;

  /* border: 1px solid; */
}

.greetingsWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.greetingsTextWrapper {
  /* border: 1px solid red; */
}

.title {
  font-size: 2.5rem;
  line-height: 3.75rem;

  font-weight: 700;
}

.discription {
  font-size: 1.2rem;
  line-height: 1.4;
  font-weight: 500;
}

@media screen and (max-width: 768px) {
  .greetingsWrapper {
    flex-direction: column-reverse;
  }

  .title {
    text-align: center;
  }
}
